import axios from "axios";

const api = axios.create({
    baseURL: process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3000', // Make sure this matches your backend port
    headers: {
        'Content-Type': 'application/json'
    },
    withCredentials: true
});

// Add request interceptor to include Clerk token from localStorage
api.interceptors.request.use(
    async (config) => {
        if (typeof window === 'undefined') return config;
        
        // Get token from local storage
        const token = localStorage.getItem('token');
        const currentOrgId = localStorage.getItem('currentOrganizationId');
        
        // Log request details for debugging
        console.log(`API ${config.method?.toUpperCase()} request to: ${config.url}`);
        
        if (token) {
            config.headers['Authorization'] = `Bearer ${token}`;
            
            // Add organization context header if available
            if (currentOrgId) {
                config.headers['X-Organization-Id'] = currentOrgId;
                console.log('Including organization context:', currentOrgId);
            } else {
                console.warn('No organization context available for this request');
            }
            
            // Log token for debugging (truncated for security)
            if (token.length > 20) {
                console.log('Using token:', token.substring(0, 15) + '...');
            }
        } else {
            console.warn('No authentication token found');
        }
        
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// Add response interceptor for error handling
api.interceptors.response.use(
    (response) => response,
    (error) => {
        console.error('API error:', error?.response?.data || error.message);
        
        // Get organization context for better error handling
        const currentOrgId = typeof window !== 'undefined' ? 
            localStorage.getItem('currentOrganizationId') : null;
        
        // Handle specific error conditions
        if (error.response?.status === 401) {
            // Unauthorized - token is invalid or missing
            console.error('Authentication error: Your session may have expired');
            if (typeof window !== 'undefined') {
                window.dispatchEvent(new CustomEvent('auth:unauthorized'));
            }
        } else if (error.response?.status === 400 && 
                  error.response?.data?.message?.includes('organization')) {
            // Organization access issue
            console.error('Organization access error:', error.response.data.message);
            
            // Log additional details for debugging
            if (typeof window !== 'undefined') {
                console.log('Current organization ID:', currentOrgId);
                console.log('Requested URL:', error.config?.url);
                console.log('Request details:', {
                    method: error.config?.method,
                    params: error.config?.params,
                    data: error.config?.data
                });
            }
        }
        
        return Promise.reject(error);
    }
);

export default api;